import React, {Component} from 'react';
import {Grid, Cell} from 'react-mdl';
import img from '../me-at-work.jpg';
class AboutMe extends Component {
    render() {
        return (
            <Grid>
                
                <Cell col={6}>                
                <img src={img} align="center" alt="me" className="avatar-img" style={{marginLeft: '25%'}}/>
                </Cell>

                <Cell className="aboutme-text" col={6} style={{paddingTop: "3%"}}>
                This May, I will graduate summa cum laude from Pace University’s Seidenberg School of Computer Science and Information Systems. Writing and testing code fulfills me! I am actively seeking employment that will enable me to continue with my passion. At the start of my senior year, I was awarded a paid internship as the Software Development Engineer for Seidenberg’s Blue CoLab, a collaborative laboratory that applies STEM to the innovation of real-time water monitoring. At the CoLab, I have been developing software that collects real-time data from deployed water sensors, and translates that information for a consumer app that monitors water quality. When contamination is detected, the app triggers a warning to the water user. The opportunity to devote myself to such a complex project has heightened my desire to make creative coding my career. I am eager to speak with any firm interested in a committed and passionate programmer who is ready to work. 
                </Cell>

            </Grid> 
        );
    }
}

export default AboutMe;